import React from 'react';
import {connect} from "react-redux";
import {AuthReducerType, logoutMeTC} from "../Redux/authReducer";
import Header from "./Header";
import {ThunkDispatch} from "redux-thunk";
import {StateType} from "../Redux/redux-store";
import {setProfileTC} from "../Redux/profileReducer";

type HeaderAPIType = MapStateToPropsType & MapDispatchToPropsType
class HeaderAPI extends React.Component<HeaderAPIType> {

    componentDidMount() {
        if(this.props.ownerID){
            this.props.setProfile(this.props.ownerID)
        }
    }

    render() {
        console.log(this.props.smallPhoto)
        console.log(this.props.ownerID)
        return (<Header {...this.props} />);
    }
}
type MapStateToPropsType = {
    auth:AuthReducerType
    smallPhoto:string | null | undefined
    ownerID:number | null
}
const mapStateToProps = (state: StateType):MapStateToPropsType => {
    return {
        auth: state.auth,
        smallPhoto:state.profilePage.profile?.photos.small,
        ownerID:state.auth.id
    }
}

type MapDispatchToPropsType = {
    logout:()=>void
    setProfile:(ownerID:number)=>void
}
const mapDispatchToProps = (dispatch:ThunkDispatch<any, any, any>):MapDispatchToPropsType => {
    return {
        logout: () => {
            dispatch(logoutMeTC())
        },
        setProfile:(ownerID:number)=>{
            dispatch(setProfileTC(ownerID))
        }
    }
}


let HeaderContainer = connect(mapStateToProps, mapDispatchToProps)(HeaderAPI)

export default HeaderContainer


